<script>
  import lottie from "lottie-web";
  import { onMount } from "svelte";
  import { link } from "svelte-spa-router";

  export let page;

  let homeLottie;
  let animationsLottie;
  let mobileMenuLottie;
  onMount(() => {
    mobileMenuLottie = lottie.loadAnimation({
      container: document.getElementById("lottie-mobile-menu"),
      renderer: "svg",
      loop: false,
      autoplay: false,
      path: "/res/lottie/menu-collapse.json",
    });
    homeLottie = lottie.loadAnimation({
      container: document.getElementById("lottie-home"),
      renderer: "svg",
      loop: false,
      autoplay: false,
      path: "/res/lottie/home.json",
    });
    animationsLottie = lottie.loadAnimation({
      container: document.getElementById("lottie-animations"),
      renderer: "svg",
      loop: false,
      autoplay: false,
      path: "/res/lottie/animation-title.json",
    });

    if (page == "home") {
      homeLottie.play();
    }
    if (page == "animations") {
      animationsLottie.play();
    }
  });
  let showMobileMenu = false;

  function toggleMobileMenu(event) {
    if (showMobileMenu) {
      mobileMenuLottie.setDirection(-1);
    } else {
      mobileMenuLottie.setDirection(1);
    }
    mobileMenuLottie.play();
    showMobileMenu = !showMobileMenu;
  }
</script>

<style>
  .header-bg {
    padding: 0px;
    background-color: #e6e6e6;
  }

  .no-pad {
    margin: 0px;
  }

  .header-menu {
    background-color: #e6e6e6;
    height: 100px;
  }

  .icon {
    position: relative;
  }

  .icon:after {
    content: "\A";
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.6);
    opacity: 0;
    transition: all 0.5s;
    -webkit-transition: all 0.5s;
  }

  .icon:hover:after {
    opacity: 1;
  }
</style>

<div class="row no-pad">
  <div class="col m10 s12 header-bg ">
    <div
      class="header-menu hide-on-med-and-up"
      on:click={toggleMobileMenu}
      id="lottie-mobile-menu" />
    <div class="header-menu show-on-med-and-up" />
  </div>
  <div
    class="col m1 s12 header-bg {showMobileMenu ? '' : 'hide-on-small-only'}">
    <a href="/" use:link><div class="header-menu icon" id="lottie-home" />
    </a>
  </div>
  <div
    class="col m1 s12 header-bg {showMobileMenu ? '' : 'hide-on-small-only'}">
    <a href="/animations" use:link><div
        class="header-menu icon"
        id="lottie-animations" />
    </a>
  </div>
</div>
